@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: rgb(17, 17, 17);
}
::-webkit-scrollbar {
  background: black;
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background: rgb(46, 46, 46);
  border-radius: 25px;
}

.nav {
  position: fixed;
  top: 0;
  padding: 20px;
  padding-left: 0;
  height: 30px;
  width: 100%;
  z-index: 1;
  transition: all 0.5s ease-in;
}

.nav__container {
  display: flex;
  /* justify-content: space-between; */
}

.nav__logo {
  position: fixed;
  left: 0;
  top: 8.5px;
  width: 90px;
  object-fit: contain;
  cursor: pointer;
  padding-left: 20px;
}

.nav__avatar {
  cursor: pointer;
  position: fixed;
  right: 20px;
  width: 40px;
  top: 13.5px;
}

.nav__black {
  background-image: linear-gradient(
    180deg,
    rgba(37, 37, 37, 0.808),
    rgba(0, 0, 0, 0.829)
  );
}

.qnave__fade {
  /* left: 0; */
  /* position: fixed; */
  height: 3rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(66, 66, 66, 0.61),
    rgba(31, 30, 30, 0.61) 90%
  );
  z-index: 2;
}

.nav__components {
  padding-top: 5px;
  display: flex;
  position: relative;
  left: 100px;
  list-style: none;
  justify-content: center;
}
.nav__components > a {
  margin-right: 35px;
}
.nav__components > a > li {
  box-sizing: content-box;
  text-transform: capitalize;
  padding-bottom: 4px;
  padding-left: 7px;
  padding-right: 7px;
}
.nav__components > a > li:hover {
  border-bottom: 2px solid red;
}
.active {
  border-bottom: 2px solid red;
}
.downArrow {
  display: none;
}
.in_li__downArrow {
  display: none;
}
@media only screen and (max-width: 700px) {
  .nav__components {
    position: fixed;
    width: 100%;
    height: 350px;
    left: 0;
    top: -390px;
    /* top: 0px; */
    flex-direction: column;
    z-index: 40;
    align-items: center;
    padding-top: 30px;
    padding-left: 19px;
    background-image: linear-gradient(
      180deg,
      rgba(37, 37, 37, 0.808),
      rgba(0, 0, 0, 0.829) 95%
    );
    transition: top 600ms;
  }
  .cover {
    position: fixed;
    top: 0;
    left: 0;
    height: 0;
    width: 100vw;
    background-color: rgba(27, 27, 27, 0.562);
    z-index: 30;
  }
  .set_cover {
    height: 100vh;
  }
  .dotoggle {
    top: 0 !important;
  }
  .in_li__downArrow {
    display: block;
    position: fixed;
    left: 47.5%;
    -webkit-transform: scale(1.5) rotate(180deg);
            transform: scale(1.5) rotate(180deg);
    fill: white;
  }
  .nav__components__downArrow {
    margin-top: 40px;
  }
  .nav__components > a {
    padding-bottom: 20px;
  }
  .nav__components > a:hover .nav__components > a > li {
    border-bottom: 2px solid red;
  }
  .nav__components > a {
    width: 200px;
    text-align: center;
  }

  .nav__components > a:hover .nav__components > a > li {
    border: none;
  }
  .nav__avatar {
    cursor: pointer;
    position: fixed;
    right: 0px;
    margin-right: 20px;
    width: 40px;
  }
  .nav {
    width: 100%;
    overflow-y: hidden;
  }
  .downArrow {
    display: block;
    position: fixed;
    left: 50%;
    top: 40px;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    fill: rgba(148, 148, 148, 0.75);
  }
  .hide_downArrow {
    display: none;
  }
}

.banner {
  position: relative;
  color: white;
  height: 720px;
  object-fit: contain;
  /* transform: translateY(20px); */
}

.banner__contents {
  margin-left: 30px;
  padding-top: 210px;
  height: 190px;
}

.banner__title {
  font-family: "Lobster";
  font-size: 4rem;
  font-weight: 800;
  letter-spacing: 8px;
  /* margin-left: 7S0px; */
  padding-bottom: 0.3rem;
}

.banner__description {
  line-height: 1.3;
  width: 45rem;
  font-weight: 500;
  font-size: 1.5rem;
  padding-top: 1rem;
  max-width: 600px;
  height: 80px;
}

.banner__fadeBottom {
  height: 20.1rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(31, 31, 31, 0.61),
    #111
  );
}

.banner__buttons {
  margin-top: 45px;
}
.banner__btn {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 1rem 2.9rem;
  margin-top: 20px;
  margin-left: 1rem;
  font-size: 1rem;
  background-color: rgba(32, 32, 32, 0.55);
}
.banner__btn-p {
  background-color: rgba(32, 32, 32, 0.7);
}

.banner__btn:hover {
  color: #000;
  background-color: #e6e6e6;
}

@media only screen and (max-width: 899px) {
  .banner__title {
    font-size: 2rem;
  }
  .banner__contents {
    padding-top: 270px;
  }
  .banner__description {
    font-size: 0.95rem;
    width: 250px;
  }
  .banner__fadeBottom {
    height: 11.3rem;
  }
}

@media only screen and (max-width: 899px) {
  .banner {
    height: 640px;
  }
}

.row {
  color: rgba(233, 233, 233, 0.897);
  margin-left: 20px;
}

.row__movieName {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 30px;
  margin-bottom: 15px;
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  cursor: pointer;
  max-height: 200px;
  object-fit: contain;
  margin-right: 20px;
  min-width: 260px;
  height: 140px;
  transition: -webkit-transform 450ms;
  transition: transform 450ms;
  transition: transform 450ms, -webkit-transform 450ms;
}

.row__poster:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  opacity: 1;
}

.row__posterLarge {
  min-width: 250px;
  /* height: 120px; */
  min-height: 400px;
}
.row__posterLarge:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.row__content {
  display: none;
  height: 100%;
  background: rgba(0, 0, 0, 0.541);
}
.row_content-box {
  padding-top: 19px;
}
.content__title {
  margin-bottom: 10px;
  margin-left: 30px;
  font-size: 1.3rem;
}
.content__description {
  margin-left: 30px;
  font-size: 0.8rem;
  max-width: 200px;
}

.row_content-box-large {
  padding-top: 250px;
}
.row__poster:hover .row__content {
  display: block;
}

* {
  font-family: sans-serif;
}

a {
  color: white;
  text-decoration: none;
}

.footer {
  display: flex;
}
.footer__container {
  margin: auto;
  padding-top: 70px;
  padding-bottom: 100px;
  width: 80%;
  min-width: 900px;
}
.footer__uppertext {
  color: white;
  margin-left: 40px;
  margin-bottom: 8px;
  font-weight: normal;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.content-box ul {
  list-style: none;
  margin: auto;
}
.content-box li {
  margin-top: 15px;
}

@media only screen and (max-width: 898px) {
  .content-box {
    margin-top: 40px;
  }
  .content-box li {
    font-size: 1.2rem;
  }
  .footer__container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-width: unset;
  }
  .footer__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  .footer {
    font-size: 15px;
  }
  .content-box li {
    font-size: 15px;
  }
}

@media only screen and (max-width: 498px) {
  .content-box-2,
  .content-box-4 {
    display: none;
  }

  .footer {
    font-size: 12px;
  }
  .content-box li {
    font-size: 12px;
  }
}


.loginScreen {
